<template>
  <div class="manage-header">
    <h-select
        class="manage-header__section-select"
        :items="manageSections"
        v-model="currentManageSection"
        item-value="path"
        item-text="name"
        :label="$t('manage.sections')"
        hide-message
        :show-clear-button="false"
        small
    ></h-select>
    <h-spacer></h-spacer>
    <back-to-calendar-button></back-to-calendar-button>
    <logout></logout>
  </div>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import BackToCalendarButton from '../BackToCalendarButton.vue';
import Logout from '../Logout.vue';

const manage = namespace('manage-module/main');

@Component({
  components: {
    BackToCalendarButton,
    Logout,
  },
})
class ManageHeader extends Vue {
  @manage.Getter('manageSections')
  manageSections;

  @manage.State('activeManageSection')
  activeManageSection;

  @manage.Action('openSection')
  openSection;

  get currentManageSection() {
    return this.activeManageSection;
  }

  set currentManageSection(val) {
    this.openSection(val);
  }
}

export default ManageHeader;
</script>

